	module.exports = {"data":{"post":{"html":"<p>A hearty welcome to the world to <a href=\"http://traumwind.editthispage.com/2000/09/01\">Raphael Nicolaj Spernau</a>, the latest editthispage baby! </p>\n<p><b>Cut off!</b> Our flatrate Internet provider has closed all accounts and filed for bankruptcy! Good thing we only had one month left on our prepaid account. I think we'll switch to the <a href=\"http://www.dtag.de\">Deutsche Telecom-edians</a>, er, I mean <a href=\"http://www.t-online.com\">T-Offline</a>. They now offer a flatrate of their own, and they won't be going broke any time soon. And maybe someday they'll offer T-DSL in backwoods places like Lüllau. (And maybe someday will be March 2001....)</p>","fields":{"title":"Welcome to the real world","date":"01 Sep 2000","url":"https://papascott.de/2000-09-01-welcome-to-the-real-world/"}}},"pathContext":{"slug":"/2000-09-01-welcome-to-the-real-world/","prev":{"fields":{"title":"Christopher's first ride","date":"29 Aug 2000","slug":"/2000-08-29-christophers-first-ride/"}},"next":{"fields":{"title":"Weekend off","date":"02 Sep 2000","slug":"/2000-09-02-weekend-off/"}}}}

